s, manejo ecológico de plagas y enfermedades. ")]),_c('p',{staticClass:"mb-5"},[_vm._v("Así pues, un resumen de lo visto en el presente componente podrá ser visualizado en el siguiente mapa conceptual:")]),_c('div',{staticClass:"row justify-content-center"},[_vm._m(0),_c('div',{staticClass:"col-auto"},[_c('a',{staticClass:"anexo mb-4",attrs:{"href":_vm.obtenerLink('/downloads/anexo1.pdf'),"target":"_blank"}},[_vm._m(1),_vm._m(2)])])])])],1)}
